import React, { useState, useEffect } from 'react';
import { Button, Typography, Box, Card, CardContent, CircularProgress } from '@mui/material';

function Simulation() {
  const [simulationData, setSimulationData] = useState(null);
  const [loading, setLoading] = useState(false);
  const [simulationResult, setSimulationResult] = useState([]);

  const startSimulation = async () => {
    setLoading(true);
    const response = await fetch('http://localhost:5000/start_simulation', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        agents: [
          { id: 1, strategy: 'aggressive', threshold: 1200000, cost_per_inquiry: 5000 },
          { id: 2, strategy: 'cautious', threshold: 1000000, cost_per_inquiry: 7000 }
        ],
        rounds: 10,
      }),
    });
    const result = await response.json();
    setSimulationResult(result.simulation_results);
    setLoading(false);
  };

  return (
    <Box display="flex" justifyContent="center" alignItems="center" height="100vh">
      <Card>
        <CardContent>
          <Typography variant="h5">Real Estate Agent Simulation</Typography>
          {loading ? (
            <CircularProgress />
          ) : (
            <>
              <Button onClick={startSimulation}>Start Simulation</Button>
              {simulationResult.map((round, index) => (
                <Box key={index} mt={2}>
                  <Typography>Round {round.round}:</Typography>
                  {round.actions.map((action, i) => (
                    <Typography key={i}>{action}</Typography>
                  ))}
                </Box>
              ))}
            </>
          )}
        </CardContent>
      </Card>
    </Box>
  );
}

export default Simulation;
