import React, { useState, useEffect } from 'react';
import { Button, Typography, Box, Card, CardContent } from '@mui/material';
import { useSpring, animated } from 'react-spring'; // Import react-spring

const agentsData = [
  { id: 1, status: 'searching', currentOffer: 1000000 },
  { id: 2, status: 'searching', currentOffer: 900000 }
];

const roundDuration = 2000; // Duration of each round in ms

function MainApp() {
  const [round, setRound] = useState(0);
  const [agents, setAgents] = useState(agentsData);
  const [simulationDone, setSimulationDone] = useState(false);

  const fadeAnimation = useSpring({
    opacity: 1,
    from: { opacity: 0 },
    config: { duration: 1000 }
  });

  useEffect(() => {
    const interval = setInterval(() => {
      if (!simulationDone) {
        setRound((prevRound) => prevRound + 1);
        runAgentSimulation();
      }
    }, roundDuration);

    return () => clearInterval(interval);
  }, [simulationDone]);

  const runAgentSimulation = () => {
    setAgents((prevAgents) =>
      prevAgents.map((agent) => {
        const newOffer = Math.floor(Math.random() * (1300000 - 900000 + 1)) + 900000;
        if (newOffer > agent.currentOffer) {
          return { ...agent, status: 'settling', currentOffer: newOffer };
        } else {
          return { ...agent, status: 'searching', currentOffer: newOffer };
        }
      })
    );
    checkSimulationCompletion();
  };

  const checkSimulationCompletion = () => {
    const allSettled = agents.every((agent) => agent.status === 'settling');
    if (allSettled) {
      setSimulationDone(true);
    }
  };

  return (
    <Box sx={{ padding: 4, textAlign: 'center' }}>
      <Typography variant="h4">Real Estate Agent Simulation</Typography>
      <Typography variant="h6">Round: {round}</Typography>

      <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: 4 }}>
        {agents.map((agent) => (
          <animated.div style={fadeAnimation} key={agent.id}>
            <Card sx={{ margin: 2, width: 200 }}>
              <CardContent>
                <Typography variant="h6">Agent {agent.id}</Typography>
                <Typography>Status: {agent.status}</Typography>
                <Typography>Current Offer: ${agent.currentOffer}</Typography>
                <Box sx={{ backgroundColor: agent.status === 'settling' ? 'green' : 'red', height: 50, width: 50 }} />
              </CardContent>
            </Card>
          </animated.div>
        ))}
      </Box>

      {simulationDone && (
        <Typography variant="h5" color="primary" sx={{ marginTop: 4 }}>
          Simulation Complete: All agents have settled!
        </Typography>
      )}

      {!simulationDone && (
        <Button variant="contained" onClick={runAgentSimulation} sx={{ marginTop: 4 }}>
          Next Round
        </Button>
      )}
    </Box>
  );
}

export default MainApp;
